body {
  margin: 0;
  padding: 0;
}

.header {
  height: 2.5rem;
  background-color: #fac585;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.result-header {
  background-color: #68a7ac;
  transition: all 0.1s ease;
}

.bookmarks-header-block {
  background-color: #85bafa;
  position: relative;
}

.hide-tip {
  position: absolute;
  width: 0.5rem;
  height: 0.5rem;
  border: 1rem solid #fac585;
  border-bottom: none;
  border-right: none;
  right: -1rem;
  transform: rotateZ(-45deg);
}

.hide-tip:hover {
  cursor: pointer;
}

.hide-tip:hover .hide-bookmark {
  color: #fff;
}

.hide-bookmark {
  margin: 0;
  margin-left: 0.5rem;
  transform: rotate(45deg);
  color: #eee;
  font-weight: 500;
  user-select: none;
}

.menu-icon {
  position: absolute;
  left: 1rem;
  width: 2rem;
  height: 2rem;
}

.menu-icon:hover {
  cursor: pointer;
  opacity: 0.8;
}

.show-tip {
  width: 1rem;
  height: 1rem;
  left: -1.1rem;
  border-color: #85bafa;
  transform: rotateZ(135deg);
}

.show-tip:hover {
  border-color: #6eacf7;
}

.show-tip:active {
  border-color: #5c9ff1;
}

.logo-icon {
  color: #e65c00;
  font-weight: 500;
}

.auto-type {
  font-family: "Montserrat", sans-serif;
  font-size: 1.5rem;
  color: #fff;
  width: max-content;
  white-space: pre;
}

.subtext {
  font-size: 25px;
  margin-left: 0.5rem;
  color: #fff;
}

.colored {
  color: #e65c00;
  opacity: 0.5;
}

.custom-cursor {
  display: inline-block;
  width: 5px;
  height: 30px;
  background-color: #fff;
}

@media (max-width: 800px) {
  .menu-icon {
    display: none;
  }

  .result-header {
    display: none;
  }
}
